import { i as interestedGoingSchema } from "../../../../chunks/schema3.js";
import { g as getFullName } from "../../../../chunks/member.js";
import { s as sendNotification } from "../../../../chunks/index3.js";
import { N as NotificationType } from "../../../../chunks/types3.js";
import { e as eventLink } from "../../../../chunks/redirect.js";
import { f as fail } from "../../../../chunks/index.js";
import "just-clone";
import "ts-deepmerge";
import "memoize-weak";
import { z as zod } from "../../../../chunks/zod.js";
import "devalue";
import { s as superValidate, m as message } from "../../../../chunks/superValidate.js";
import { e as eventPageLoad } from "../../../../chunks/EventPageLoad.js";
const interestedAction = (isInterested, isGoing) => async ({ request, locals }) => {
  const { prisma, user, member } = locals;
  const form = await superValidate(request, zod(interestedGoingSchema));
  if (!form.valid) return fail(400, { form });
  const event = await prisma.event.update({
    where: { id: form.data.eventId },
    data: {
      interested: {
        [isInterested ? "connect" : "disconnect"]: {
          studentId: user?.studentId
        }
      },
      going: {
        [isGoing ? "connect" : "disconnect"]: {
          studentId: user?.studentId
        }
      }
    },
    select: {
      id: true,
      slug: true,
      title: true,
      author: {
        select: {
          id: true
        }
      }
    }
  });
  if (member) {
    if (isGoing) {
      await sendNotification({
        title: `${event.title}`,
        message: `${getFullName(member)} kommer p책 ditt event.`,
        type: NotificationType.EVENT_GOING,
        link: eventLink(event),
        memberIds: [event.author.id],
        fromMemberId: member.id
      });
    } else if (isInterested) {
      await sendNotification({
        title: `${event.title}`,
        message: `${getFullName(member)} 채r intresserad av ditt event.`,
        type: NotificationType.EVENT_INTERESTED,
        link: eventLink(event),
        memberIds: [event.author.id],
        fromMemberId: member.id
      });
    }
  }
  return message(form, {
    message: `${isInterested ? "intresserad av" : isGoing ? "kommer p책" : "kommer inte/채r inte intresserad av"} event`,
    type: "hidden"
  });
};
const load = eventPageLoad();
const actions = {
  interested: interestedAction(true, false),
  going: interestedAction(false, true),
  none: interestedAction(false, false)
};
export {
  actions,
  load
};
